* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Outfit", sans-serif;
}
body main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: min(100%, 375px);
  background-color: #1A2A33;
  height: 667px;
}
body main .App {
  position: relative;
  width: 327px;
  height: 429px;
}
@media screen and (min-width: 768px) {
  body main {
    width: 768px;
    height: 1024px;
  }
  body main .App {
    width: 460px;
    height: 471px;
  }
}
@media screen and (min-width: 1440px) {
  body main {
    width: 1440px;
    height: 900px;
  }
  body main .App {
    width: 460px;
    height: 471px;
  }
}/*# sourceMappingURL=App.css.map */