
.overlay {
    width: 375px;
    height: 667px;
    background-color: black;
    position: absolute;
    left: -1.5rem;
    bottom: 26.5rem;
    opacity: 0.7;
    z-index: 1;
    margin-bottom: -34rem;
    
}
.dialogue {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: -1.5rem;
    bottom: 5rem;
    width: 375px;
    height: 228px;
    background-color: #1F3641;
    z-index: 2;
    text-align: center;
    
    

    .winner-loser-message {
        color: #A8BFC9;
        font-weight: bold;
    }

    .winner-of-round {
        font-size: 24px;
        

        .winner-of-round-xlogo, .winner-of-round-ologo {
            position: relative;
            top: 6px;
            width: 30px;
            height: 30px;
            margin-right: 10px;
        }
    }

    .quit-button {

        width: 76px;
        height: 52px;
        color: #1A2A33;
        font-weight: bold;
        background-color: #A8BFC9;
        border: none;
        border-radius: 12px;
        box-shadow: inset 0px -4px 0px #6B8997;
        font-size: 1rem;
        margin: 0 1rem;

    }

    .nextround-button {
        width: 146px;
        height: 52px;
        color: #1A2A33;
        background-color: #F2B137;
        font-weight: bold;
        border: none;
        border-radius: 12px;
        box-shadow: inset 0px -4px 0px #CC8B13;
        font-size: 1rem;
        
    }
}

@media screen and (min-width: 768px) {

    

    .overlay {

        width: 768px;
        height: 1024px;
        left: -9.5rem;
        bottom: 16.8rem;
        
        
    }

    .dialogue {
        width: 768px;
        height: 266px;
        left: -9.6rem;
        overflow: hidden;

        .winner-loser-message {
            margin-bottom: -3rem;
        }

        .winner-of-round {
            font-size: 40px;
            margin-bottom: 1rem;
            
    
            .winner-of-round-xlogo, .winner-of-round-ologo {
                position: relative;
                top: 14px;
                width: 64px;
                height: 64px;
                margin-right: 10px;
            }
        }
    }
    
}

@media screen and (min-width: 1440px) {

    .overlay {
        height: 900px;
        width: 1440px;
        left: -30.5rem;
        bottom: 20.5rem;
    }
    .dialogue {
        width: 1440px;
        left: -30.5rem;

        .quit-button:hover, .nextround-button:hover {
            cursor: pointer;
        }
    }
}